@media only screen and (max-width: 800px) {
    /* Title block */
    .titleSection {
        width: 100%;
        padding-left: 25px;
        padding-right: 25px;
        margin-top: 100px;
    }
    .titleBlock {
        width: 100%;
    }
    .nameTitle { 
        font-size: 2em;
    }
    .nameDescription {
        font-size: 1.5em;
    }

    /* About Me */
    .aboutMeSection {
        width: 100%;
        padding-left: 25px;
        padding-right: 25px;
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: 450px 1fr;
        grid-gap: 30px;
        gap: 30px;
    }

    .aboutMeText {
        font-size: 0.9em;
    }

